<template>

    <main class="flex-1 relative z-0 overflow-y-auto focus:outline-none" tabindex="0">
      <nav class="bg-white sticky top-0 z-10 shadow">
        <div class="mx-auto px-6">
          <div class="h-14 flex justify-between">
            <div class="hidden sm:flex"><a
              class="inline-flex items-center font-mono uppercase text-sm pt-1 border-b-3 transition duration-200 ease-in hover:no-underline focus:outline-none text-gray-600 border-transparent hover:border-primary hover:text-primary" :class="{'text-primary border-primary':!toAccountStatus}"
              @click="toAccount">Account</a>
<!--              <a-->
<!--              class="inline-flex items-center font-mono uppercase text-sm pt-1 border-b-3 transition duration-200 ease-in hover:no-underline focus:outline-none text-gray-600 border-transparent hover:border-primary hover:text-primary ml-6" :class="{'text-primary border-primary':toAccountStatus}"-->
<!--              @click="toBilling">Billing</a>-->
            </div>
          </div>
        </div>
      </nav>
      <router-view></router-view>
    </main>

</template>

<script>
export default {
  name: 'settings',
  data() {
    return {
      toAccountStatus: true,
    };
  },
  methods: {
    toAccount() {
      this.$router.push({ path: '/dashboard/settings/account' });
    },
    toBilling() {
      this.$router.push({ path: '/dashboard/settings/billing' });
    },
  },
  watch: {
    $route(to) {
      this.toAccountStatus = (to.path.indexOf('account') === -1);
    },
  },
  mounted() {
    const to = this.$route;
    this.toAccountStatus = (to.path.indexOf('account') === -1);
  },
};
</script>

